.route-tile {
  border-width: 1px;
  border-color: black;
  border-style: solid;
  width: 30%;
  margin: 1%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.route-name {
  font-size: 1em;
  text-align: center;
  height: 10%;
  width: 95%;
}

.route-thumbnail.landscape {
  max-width: 100%;
  height: auto;
}

.route-thumbnail.portrait {
  max-width: 56%;
  height: auto;
}

.route-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.route-info div {
  text-align: center;
}

@media screen and (max-width: 600px) {
  .route-name {
    font-size: 0.75em;
  }

  .route-info div {
    font-size: 0.75em;
  }
}
