.route-modal {
  padding: calc(1400px - 100vh) 2rem 2rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  max-height: 90vh;
  max-width: 90vw;
}

.route-description {
  text-indent: 2rem;
}

.route-map {
  width: 100%;
  margin: 1rem;
  object-fit: contain;
}

@media screen and (max-width: 600px) {
  .route-modal {
    padding: calc(1020px - 100vh) 1rem 1rem 1rem;
  }
}
